import React, {useState, useEffect} from 'react';
import {useIntl} from 'react-intl';
import AppsContainer from '../../../@crema/core/AppsContainer';
// import AppComponentCard from '@crema/core/AppComponentCard';
import {AppComponentHeader} from '@crema';
import {TextField} from '@mui/material';
import {Box} from '@mui/system';
import {MobileDateRangePicker} from '@mui/lab';
import CAppContaner from 'components/CappContaner';
import {get_Employee_Report} from 'query/orderReoprt/getOrder';
import {useLazyQuery} from '@apollo/client';
import ReportInvoiceEmployee from 'pages/ecommerce/ReportInvoiceEmployee';

const EmployeesReports = () => {
  const {messages} = useIntl();
  const [isLoading, setIsloding] = useState(true);
  const [value, setValue] = React.useState([new Date(), new Date()]);
  const [getEmployeeReport, {loading, error, data}] =
    useLazyQuery(get_Employee_Report);
  const handleSubmit = async () => {
    setIsloding(true);
    let firstDate = '';
    let seconDate = '';
    if (value[0] != null) firstDate = dateForm(value[0]);
    if (value[1] != null) seconDate = dateForm(value[1]);
    getEmployeeReport({
      variables: {startDate: firstDate, endDate: seconDate},
    });
  };
  useEffect(() => {
    console.log(data);
    console.log(error);
  }, [loading]);
  useEffect(() => {
    if (data) {
      setIsloding(false);
      console.log(data);
    }
  }, [data]);
  const dateForm = (date) => {
    var dd = String(date.getDate()).padStart(2, '0');
    var mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = date.getFullYear();
    date = mm + '/' + dd + '/' + yyyy;
    return date;
  };
  return (
    <>
      <AppComponentHeader
        cardStyle={{height: 'auto'}}
        fullView
        title={messages['sidebar.reports.employees']}
      />
      <CAppContaner
        title={messages['Datepicker.title']}
        buttonCompMethod={handleSubmit}
        component={() => (
          <>
            <MobileDateRangePicker
              startText={messages['sidebar.reports.startdate']}
              endText={messages['sidebar.reports.enddate']}
              value={value}
              onChange={(newValue) => {
                setValue(newValue);
              }}
              renderInput={(startProps, endProps) => (
                <React.Fragment>
                  <TextField {...startProps} />
                  <Box sx={{mx: 2}}> to </Box>
                  <TextField {...endProps} />
                </React.Fragment>
              )}
              buttonComp={() => {}}
            />
          </>
        )}
      />

      {!isLoading ? (
        <>
          {' '}
          <Box sx={{mx: 2}} padding={5}>
            <ReportInvoiceEmployee data={data.getEmployeeReport} />
          </Box>
        </>
      ) : (
        <AppsContainer fullView></AppsContainer>
      )}
    </>
  );
};
export default EmployeesReports;
